<template>
  <div class="container">
    <div class="login" v-if="!userInfo">
      <wux-button
        block
        type="positive"
        class="login__button"
        open-type="getUserInfo"
        @getuserinfo="bindGetUserInfo"
      >登录</wux-button>
      <img src="/static/img/back.png">
    </div>
    <wux-dialog id="wux-dialog"/>
  </div>
</template>

<script>
import store from "../../store";
import { $wuxDialog } from "../../wux/index";
export default {
  computed: {
    userInfo() {
      return store.state.userInfo;
    }
  },
  data() {
    return {};
  },
  methods: {
    alert() {
      $wuxDialog().alert({
        resetOnClose: true,
        title: "提示",
        content: "请先授权!"
      });
    },
    bindGetUserInfo(e) {
      let _this = this;
      if (e.mp.detail.rawData) {
        store.commit("userInfo");
        _this.$router.back()
      } else {
        _this.alert();
      }
    }
  }
};
</script>

<style scoped>
.login {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  overflow: hidden;
  background: #f8fbff;
}
.login img {
  width: 100%;
  display: block;
  height: 380px;
}
.login__button {
  overflow: hidden;
  position: absolute;
  width: 80%;
  left: 10%;
  bottom: 100px;
}
</style>



